<template lang="pug">
  
  .write-message
    .message-header
      .message-header__wrapper
        .message-header__back(@click='goBack')
          svg.message-header__arrow
            use(xlink:href='#arrow')

        p.message-header__title Новое сообщение
    
    .write-message__recieve
      p.write-message__title Кому:
      input(type='number' placeholder='Номер получателя' v-model.trim='number').write-message__input
      svg(@click='select = true').write-message__add
        use(xlink:href='#Add')

    .send-message
      input(type='text' placeholder='Сообщение' 
            v-model.trim='message' @keyup.enter='sendMessage' ).send-message__input
      button(@click='sendMessage').send-message__send
        img(src='@/assets/img/icons/send.svg').send-message__icon

    transition(name='fade')
      SelectContact(v-if='select' @close='select = false' @user='setUser')

</template>

<script>

  import {mapState} from 'vuex'
  import SelectContact from './SelectContact'

  export default {
    components: { SelectContact },
    data() {
      return {
        number: '',
        message: '',
        select: false
      }
    },
    computed: mapState({
      blackList: state => state.calls.blocked,
      phoneNumber: state => state.player.phoneNumber
    }),
    methods: {
      setUser(user) {
        this.number = user.number
        this.$alert.show(`Выбран контакт: ${user.name}`)
      },
      sendMessage() {
        if (!this.message || !this.number)
          return this.$alert.show('Заполните все поля');

        else if (this.blackList.includes(parseInt(this.number))) 
          return this.$alert.show('Номер в черном списке')

        else if (this.number == this.phoneNumber)
          return this.$alert.show('Введен собственный номер')

        this.$store.dispatch('SEND_MESSAGE', {msg: this.message, to: this.number});
        this.$router.push({name: 'MessageUser', params: {number: this.number}})
        this.$alert.show('Сообщение отправлено')
      },
      goBack() {
        this.$router.go(-1)
      }
    },

  }

</script>

<style lang="sass">

  @import '../../assets/sass/phone/apps/messages/write'

</style>