<template lang="pug">
  
  div
    p(v-if='!sortedContacts.length').phone__info.
      {{ this.search ? "Нет совпадений" : "Нет контактов" }}
      
    .contact-item(v-for='(item,index) in sortedContacts')
      h1.contact-item__char-index {{item.firstChar}}
      .contact-item__name-wrapper(v-for='(user,i) in item.users') 
        .contact-item__link(@click='routerLink(user)')
          p.contact-item__name {{user.name}}

</template>

<script>

  import {mapState} from 'vuex'

  export default {
    props: {
      search: String,
      from: String
    },
    methods: {
      routerLink(user) {
        if (this.from == 'contacts')
          this.$router.push(
            {name: 'ContactInfo', params: {phone: user.number}}
          )

        else this.$emit('getUser', user);
      }
    },
    computed: {
      ...mapState({
        contacts: state => state.calls.contacts
      }),
      sortedContacts() {
        let searchedContacts,
            filteredContacts = [];

        if (!this.search)
          searchedContacts = [...this.contacts]
        else 
          searchedContacts = this.contacts.filter(({name}) => 
            name.toLowerCase().includes(this.search.toLowerCase())
          )

        searchedContacts.sort((a,b) => {
          if (a.name.toLowerCase() < b.name.toLowerCase()) return -1
        })

        searchedContacts.forEach((item,i) => {
          let name = item.name[0].toUpperCase();

          if (i === 0 || searchedContacts[i - 1].name[0].toUpperCase() != name) {
            filteredContacts.push({
              firstChar: name,
              users: []
            })
          }
          filteredContacts.forEach(key => {
            if (key.firstChar == name) {
              key.users.push(item)
            }
          })
        })
        return filteredContacts
      }
    },
  }

</script>

<style lang="sass">

  @import '../../assets/sass/phone/apps/calls/contactsList'

</style>