<template lang="pug">
  
  .select-contact
    .select-contact__header
      .nav
        h1.select-contact__title Контакты
        button(@click='close').nav__option Отменить
      
      .header-search
        svg.header-search__icon
          use(xlink:href='#Search')
        input(
          type='text'
          placeholder='Поиск'
          v-model.trim='search').header-search__input

    ContactItem(:search='search' :from="'messages'"  @getUser='sendUser').select-contact__list

</template>

<script>

  import ContactItem from '../Calls/ContactItem'

  export default {
    components: { ContactItem },
    data() {
      return {
        search: ''
      }
    },
    methods: {
      sendUser(user) {
        this.$emit('user', user)
        this.close()
        
      },
      close() {
        this.$emit('close')
      }
    }
  }

</script>

<style lang="sass">

  @import '../../assets/sass/phone/apps/messages/select'

</style>